import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import QA from '@/components/QA'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'QA',
      component: QA
    },
    {
      path: '/QA',
      name: 'QA',
      component: QA
    }
  ]
})



