<template>
    <div>
        <div class="qa-background">
            <div>{{keybordHeight}}</div>
            <!-- <div v-for="(item, index) in chats">
                <div class="chat-time" v-if="!index||(getDate(item.CreateTime)-getDate(chats[index-1].CreateTime))/(1000*60)>1">
                    {{item.CreateTime}}
                </div>
                <div v-if="item.MsgPath=='Server2Consumer'">
                    <div class="robot">
                        <img src="../assets/robot.png" style="width:50px; height:50px;" />
                        <div v-if="item.MsgType== 'image'">
                            <img v-bind:src="item.PicUrl" />
                        </div>
                            <div v-if="item.MsgType== 'news'">
                                <div class="chat-answer-article">
                                    <div class="article-item" v-for="(article,index) in item.Articles" v-if="item.Articles.length>1">
                                        <div v-if="!index">
                                            <img v-bind:src="article.PicUrl">
                                            <p>
                                                <a v-bind:href="article.Url" target="_blank">{{article.Title}}</a>
                                            </p>

                                        </div>
                                        <div v-if="index">
                                            <p class="pull-left small">
                                                <a v-bind:href="article.Url" target="_blank">{{article.Title}}</a>
                                            </p>
                                            <img v-bind:src="article.PicUrl" class="pull-right">
                                            <div class="clearfix"></div>
                                        </div>

                                    </div>
                                    <div class="article-single" v-if="item.Articles.length==1">
                                        <div class="article-item">
                                            <div>
                                                <img v-bind:src="item.Articles[0].PicUrl" />
                                                <p>
                                                    <a v-bind:href="item.Articles[0].Url">
                                                        {{item.Articles[0].Title}}
                                                    </a>
                                                </p>
                                            </div>
                                        </div>
                                        <div class="article-description">
                                            {{item.Articles[0].Description}}
                                        </div>
                                        <div class="article-href">
                                            <a v-bind:href="item.Articles[0].Url" target="_blank">查看详情</a>
                                        </div>

                                    </div>
                                </div>
                            </div>
                            <div v-else ="item.MsgType == 'text'">
                                <div v-html="item.Content"></div>
                            </div>
                        </div>
                        <div class="clearfix"></div>
                    </div>
                    <div v-if="item.MsgPath=='Consumer2Server'">
                        <div class="me">
                            <img src="../assets/me.png" />
                            <div>
                                <span>{{item.Content}}</span>
                            </div>
                        </div>
                        <div class="clearfix"></div>
                    </div>
                </div>
                <div id="bottom"></div>

            </div>
            <div class="sender-container">
                <div class="inputContainer">

                    <input placeholder="输入您的问题" v-model="query" class="query-input" id="queryInput" v-on:click="goBottom" @keyup.enter="sendQuery" type="text" />
                    <img src="../assets/send.png" class="send" v-on:click="sendQuery">

            </div>
                    <div class="voiceContainer" v-if="voiceTab">
                        <img src="../assets/yysr.png" />
            </div>
                        <div class="btnContainer" v-if="voiceTab">
                            <div class="clearBtn">清空</div>
                            <div class="sendBtn">发送</div>
                        </div> -->
                    </div>
                </div>
</template>

<script>
import { Toast } from "we-vue";
import { setTimeout } from 'timers';
export default {
    name: "QA",
    data() {
        return {
            chats: [],
            query: "",
            voiceTab: false,
            keybordHeight:''
        };
    },
    mounted() {
        this.getHistory();
        this.data.keybordHeight = this.getHeight("keybordHeight");
        // alert("页面初始窗口高度：" + window.innerHeight)
    },
    methods: {
        getHeight(name) {
            var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
            var r = window.location.search.substr(1).match(reg); //获取url中"?"符后的字符串并正则匹配
            var context = "";
            if (r != null)
            context = r[2];
            reg = null;
            r = null;
            return context == null || context == "" || context == "undefined" ? "" : context;
        },
        switchTab: function() {
            this.voiceTab = !this.voiceTab;
        },
        goBottom: function() {
            // setTimeout(() => {
            //     alert("input框被抬起时窗口高度：" + window.innerHeight)
            // },2000)
        },
        goInput: function() {
             setTimeout(function() {
                document.querySelector("#queryInput").scrollIntoView(true);
            }, 200);
        },
        getDate: function(str) {
            return new Date(str);
        },

        detectPhoneEmail: function(message) {
            //手机号
            var temp1 = message.replace(/(\d{3})\d{4}(\d{4})/g, function(
                match,
                p1
            ) {
                return '<a href="tel:' + match + '">' + match + "</a>";
            });
            var temp2 = temp1.replace(/(\w+?@\w+?\x2E.+)/g, function(
                match,
                p2
            ) {
                return '<a href="mailto:' + match + '">' + match + "</a>";
            });
            temp2 = temp2.replace(/\n/g, "<br>");
            return temp2.replace(/\r\n/g, "<br>");
        },
        submit: function(event) {
            event.preventDefault(); //阻止form表单默认提交
            this.$options.methods.sendQuery();
        },
        getHistory: function() {
            let url = "/zhiliao_kefu/v1/history/chat";
            let params = {
                apiKey: this.$route.query.apiKey,
                consumerId: this.$route.query.jobId,
                getAutoChat: true,
                getKefuChat: false,
                kefuId: "知了",
                num: 26,
                start: 1
            };

            this.$http.post(url, params).then(function(res) {
                if (res.status == 200) {
                    let that = this;
                    this.chats = res.body.result.recordList.map(function(item) {
                        if (item.Content) {
                            let message;
                            if (
                                item.MsgPath == "Server2Consumer" &&
                                 item.MsgType != "news"
                            ) {
                                message = that.$options.methods.detectPhoneEmail(
                                    item.Content
                                );
                            } else {
                                message = item.Content.replace(/\r\n/g, "<br>");
                                console.log(message)
                                message = message.replace(/\n/g, "<br>");
                            }

                            return {
                                ApiKey: "b7a02c73bb447368677a3378eac3ba76",
                                UserId: "suxiaoyan",
                                ConsumerId: '访客91722040',
                                MsgType: item.MsgType,
                                MsgSrc: item.MsgSrc,
                                CreateTime: item.CreateTime,
                                Content: message,
                                MsgPath: item.MsgPath
                            };
                        }
                        return item;
                    });
                    this.$nextTick(function() {
                        this.goBottom();
                    });
                } else {
                    Toast.text({ message: res.status + "请求出错" });
                }
            });
        },
        sendQuery: function() {
            if (!this.query) {
                Toast.text({ message: "请填写内容" });
                return;
            }
            let url = "/zhiliao_kefu/v1/consumer/api/question";
            let params = {
                ApiKey: "b7a02c73bb447368677a3378eac3ba76",
                UserId: "suxiaoyan",
                ConsumerId: '访客91722040',
                MsgType: "text",
                MsgSrc: "page",
                CreateTime: parseInt(new Date().getTime() / 1000),
                Content: this.query,
                MsgPath: "Consumer2Server"
            };
            this.chats.push(params);
            this.$nextTick(function() {
                this.goBottom();
                this.query = "";
            });

            this.$http.post(url, params).then(function(res) {
                if (res.status == 200) {
                    let message = res.body;
                    if (
                        message.MsgPath == "Server2Consumer" &&
                        message.MsgType != "news"
                    ) {
                        message.Content = this.$options.methods.detectPhoneEmail(
                            message.Content
                        );
                    }

                    this.chats.push(message);
                    this.$nextTick(function() {
                        this.goBottom();
                    });
                } else {
                    Toast.text({ message: res.status + "请求出错" });
                }
            });
            return false;
        }
    }
};
</script>
   
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import "../assets/css/qa.css";
</style>



